.story__item {
  list-style-type: none;
}
.story__highlights-title {
  color: $bg-main;
  padding-top: 54px;
  margin-bottom: 11px;
  size: 18px;
  line-height: 26px;
}
.story__highlights-numbering {
  color: $bg-main;
  margin-bottom: 17px;
}
.story__highlights-description {
  color: $bg-main;
  margin-bottom: 17px;
}
.story__highlights-item {
  background-color: #81B3D2;
  list-style-type: none;
  min-height: 211px;
}

.story__list {
margin: 0px;
padding: 0px;
}
.story__items {
