  text-align: start;
  padding-left: 65px;
}
.story__description {
  size: 14px;
  line-height: 21px;
  text-align: start;
  padding-left: 65px;
}
.title__story {
  margin-top: 33px;
}
.story__title-rank {
  color: $bg-title;
  size: 18px;
  line-height: 26px;
  margin-top: 24px;
  margin-bottom: 20px;
}
.story__description-rank {
  size: 18px;
  line-height: 26px;
  margin-bottom: 49px;
}
